.footer {
  font-size: 14px;
  background: #1475a2;
  color: #fff;
  letter-spacing: 0.2em;
  padding: 31px 0 40px;
  overflow: hidden;

  &-content {
    margin: auto;
    width: min-content;
    display: flex;
  }

  .our-works {
    width: max-content;

    div {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 20px;

      p {
        padding: 10px;
      }

      a:not(:nth-of-type(1)) {
        border-right: 2px solid #c4c4c4;
        padding-right: 10px;
        margin-left: 18px;
      }
    }
  }

  .footer-contacts {
    display: grid;
    grid-template-columns: max-content 25px auto;
    @media (min-width: 768px) {
      grid-template-columns: min-content 25px auto;
    }
    grid-template-rows: repeat(4, max-content);
    grid-column-gap: 20px;
    grid-row-gap: 30px;
    margin-right: 30px;

    p {
      &:nth-child(1), &:nth-child(3) {
        display: flex;
        align-items: center;
      }
    }

    > * {
      display: inline;
    }

    .info-1-column-item {
      text-align: right;
    }
  }

  h3 {
    margin-bottom: 20px;
  }

  ul {
    display: grid;
    grid-template-columns: max-content max-content;
    grid-template-rows: repeat(4, max-content);
    grid-gap: 28px 24px;
    list-style: url("../../images/list-arrow.png");

    li {
      text-transform: uppercase;
    }
  }
}
@media (max-width: 1450px) {
  .footer {
    font-size: 13px;
  }
}

@media (max-width: 1330px) {
  .footer {
    font-size: 0.85vw;
  }
}

@media ( min-width: 769px ) {
  .footer {
    .our-works {
      h3 {
        padding-left: 12.1vw;
      }
    }
  }
}

@media ( min-width: 1630px ) {  
  .footer {
    .our-works {
      h3 {
        padding-left: 9.1vw;
      }
    }
  }
}

@media (max-width: 768px) {
  .footer {
    font-size: 1.3vw;
    flex-direction: column;

    h3 {
      font-size: 16px;
    }

    .footer-contacts {
      img {
        width: 15px !important;
      }
    }
    .footer-content {
      flex-direction: column;
      align-items: center !important;
    }

    .our-works {
      margin-top: 40px;

      .social-media {
        img {
          width: 16px;
        }
      }
    }
  }
}

@media (max-width: 580px) {
  .footer {
    font-size: 1.6vw;
    .our-works {
      padding-left: 10px;
      padding-right: 20px;
    }
  }
}

@media (max-width: 400px) {
  .footer {
    font-size: 1.6vw !important;

    h3{
        font-size: 10px;
    }

    .footer-contacts {
      padding-left: 10px;
      grid-column-gap: 10px;
      font-size: 1.9vw !important;
    }

    .our-works {
      padding-left: 10px;
      font-size: 1.5vw !important;
    }
    .contact-info {
      justify-content: center !important;
    }
    
  }
}
